import { computed, inject, reactive, ref } from "vue";
import { emitter, EVENT_STRINGS } from "@/event-bus";

import Service from "@/service/art-cycle.js";
import { useStore } from "vuex";

export function usePatientSearch() {
    const store = useStore();

    const title = ref("Female");
    const demographic = ref();
    const fillteredDemographic = ref();
    const loading = inject("loading");

    const searchDemographic = (event) => {
        Service.searchDemographic({
            term: event.query.trim(),
            sex: "F".toLowerCase()
        })
            .then((resp) => {
                fillteredDemographic.value = resp.data;
            })
            .catch((err) => {
                emitter.emit(EVENT_STRINGS.LOGOUT);
            });
    };

    const onDemographicSelect = ({ value: { demographicNo } }) => {
        loading.value = true;
        store
            .dispatch("artCycle/getCyclesDetails", demographicNo)
            .then(() => (loading.value = false));
    };

    return {
        store,
        title,
        demographic,
        fillteredDemographic,
        loading,
        searchDemographic,
        onDemographicSelect
    };
}

export function useArtCycleForm() {
    const store = useStore();
    const selectedCycle = computed(() => store.state.artCycle.selectedCycle);
    const cycleTypeOptions = computed(
        () => store.state.artCycle.cycleTypeOptions
    );
    const spermSourceOptions = computed(
        () => store.state.artCycle.spermSourceOptions
    );
    const eggSourceOptions = computed(
        () => store.state.artCycle.eggSourceOptions
    );

    const cycle = reactive({
        cycleId: null,
        cycleType: null,
        funded: null,
        cancelled: null,
        spermSource: null,
        eggSource: null,
        eggDonor: null,
        complete: null
    });

    const fillupForm = (data) => {
        cycle.cycleId = data.cycleId;
        cycle.cycleType = data.cycleType;
        cycle.cancelled = data.cancel;
        cycle.spermSource = data.spermSource;
        cycle.eggSource = data.eggSource;
        cycle.complete = data.cycleCompleted;
    };

    const loading = inject("loading");


    return {
        store,
        selectedCycle,
        cycleTypeOptions,
        spermSourceOptions,
        eggSourceOptions,
        cycle,
        fillupForm,
        loading,
    };
}
